<template>
  <div class="demo">
    <h1>CloudArcade</h1>
    <h4>Pick The Number of Game and Check The Corresponding Price
    </h4>
    <div class="light_box">
    <div style="padding:10px;display:inline;">
      <div></div>
      Amount of Ether Need to Pay:
      <el-input
        placeholder="0 Ether"
        v-model="amount"
        :disabled="true" style="width:20%;">
      </el-input>
    </div>
    <el-button v-on:click="clickReset" type="primary" icon="el-icon-delete">Reset</el-button>
    <el-button v-on:click="clickPayment" type="primary" :loading="button_.btn_loading" :disabled="chosenGame?false:true">Pay</el-button>
    <el-row type="flex" style="padding: 10px;">
    <el-col :span="9" v-for="(game,i) in games_available" :key="i">
      <el-card :body-style="{ padding: '0px' }" style="margin:0.3em;" >
        <div style='padding:10px; box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);'>{{ game.gameID }}</div>
        <el-image
        style="width: 15em; height: 15em; margin-top:1em;"
        :src="game.picurl"
        fit="fill"></el-image>
        <!-- <img :src="game.picurl" class="image"> -->
        <div style="padding: 7px;">
          <div class='sc-condition' style="margin: auto; width:92%;">
          <div>
            CROWDNESS
          </div>
            <el-progress style="margin-top:0.5em;" :text-inside="true" :stroke-width="22" :percentage="handler(gameStatus, i)"></el-progress>
          </div>
          <div class="bottom clearfix" style="margin-top:14px;">
            <el-button style="width:93%;" type="warning" class="button" v-on:click="clickNumber(i)">Query Price</el-button>
          </div>
        </div>
      </el-card>
      </el-col>
    </el-row>
    </div>
    <div style="width: 60%;">
      <div style="margin-top: 40px;">Resend Transaction</div>
      <el-input style="width: 60%; margin-top:14px;" v-model="inputTX" placeholder="Input Transaction Hash Code"></el-input>
      <el-button @click="resendTX"> Resend info </el-button>
    </div>
    <div class = 'text-align:center; height:40px;'>
      <img v-if="pending" id="loader" src="https://loading.io/spinners/double-ring/lg.double-ring-spinner.gif">
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: 'demon',
  data () {
    return {
      amount: 0,
      pending: false,
      winEvent: null,
      transactionOver: false,
      chosenGame: 0,
      games_available:null,
      activeNames: ['1'],
      button_:{
        btn_loading: false,
      },
      gameStatus: null,
      url:null,
      inputTX:null
     
    }
  },
  computed:{
  },
  created(){
    this.serverURL = 'http://192.168.5.110',
    this.getGames(),
    this.getGammesStatus()
  },
  methods: {
    resendTX(){
      this.pending = true
      this.paymentRequest(this.inputTX, this.$store.state.web3.coinbase)
    },
    handler(gameStatus, i){
      if(gameStatus){
        let length = gameStatus.length
        if(i < length){
          return ((gameStatus[i].total - parseInt(gameStatus[i].totalAvailable, 10)) / gameStatus[i].total) * 100
        }
        else{
          return 100
        }
      }else{
        return 100
      }   
    },
    getGammesStatus(){
      this.$http.get(this.serverURL + '/getServiceCrowdness',{emulateJSON: true , timeout:10000})
      .then((data)=>{
        let status = data.body.status
        if(status == 'success'){
          this.gameStatus = data.body.result
          console.log(this.gameStatus[0])
        }else{
          this.$message.error('Problems in Server Sql')
        }
      }, error=>{
        console.log(error)
        this.$message.error('Fail to Get Games Status from GaaS')
      })
    },
    getGames (){
      this.$http.get(this.serverURL + '/getAllGames',{emulateJSON: true, timeout:10000}).then((data)=>{
        this.games_available = data.body
        console.log("gameavailable",this.games_available)
      }, error=>{
        this.$message.error('Fail to Get Games info from GaaS')
      })
    },
    paymentRequest(transactionHx, payerAddress){
      let params = {
        transactionHx: transactionHx,
        payerAddress: payerAddress
      }
      this.$http.post(this.serverURL + '/paymentRequest', params,{emulateJSON: true, timeout:35000}).then((data)=>{
        console.log("value",data.body)
        let status = data.body.status
        this.pending = false
        if(status == 'success'){
          let url = data.body.msg
          if(!url){
            return this.$message.error("Wrong in final back")
          }
          this.url = url
          const h = this.$createElement;
          this.$msgbox({
            title: 'Message',
            message: h('p', null, [
              h('span', null, 'Your Game Connection URL '),
              h('i', { style: 'color: teal' }, url)
            ]),
            showCancelButton: false,
            confirmButtonText: 'Confirm',
          })
          this.getGammesStatus()
        
        }else{
          if(data.body.code == 0){
            this.inputTX = transactionHx
            const h = this.$createElement
            this.$msgbox({
            title: 'Message',
            message: h('p', null, [
              h('span', null, 'Error In Querying Transaction, please copy the transaction hash for further check.'),
              h('i', { style: 'color: teal' }, "Transaction Already Copied")
            ]),
            showCancelButton: false,
            confirmButtonText: 'Confirm',
          })
          }else{
            this.$message.error(data.body)
          }
        }
      }, error=>{
        this.pending = false
        this.$message.error('Fail In Requiring Service From Cloud Server')
      })
    },
    clickNumber (game_id) {
        this.getGammesStatus()
        console.log("gameid", game_id + 1)
        this.chosenGame = game_id + 1
        this.pending = true
        this.$store.state.contractInstance().methods.returnPrice(this.chosenGame).call({ from : this.$store.state.web3.coinbase })
      .then(result=>{
        console.log(result)
        this.amount = parseInt(result, 10)
        this.pending = false
      }, error=>{
        this.$message.error('Fail to get game price from the smart contract')
        console.log(error)
      })
      
    },

    clickReset(){
      this.chosenGame = null
    },

    // pay the corresponding tokens to the game
    clickPayment () {
      this.getGammesStatus()
      console.log(this.gameStatus, this.chosenGame-1)
      let crowd = this.handler(this.gameStatus, this.chosenGame-1)
      console.log("crowd value", crowd)
      if(crowd === 100){
        this.$notify({
          title: 'Fail Paid',
          message: 'Transaction Fail, Because the game is full',
          type: 'warning'
        });
        return
      }
      this.button_.btn_loading = true
      this.$store.state.contractInstance().methods.moneyTransfer(this.chosenGame).send({
          from : this.$store.state.web3.coinbase,
          gas: 300000,
          value: this.$store.state.contractInstance().utils.toWei(this.amount.toString(), 'ether'),
      },(error, transactionhash)=>{
        if(error){
          this.$notify({
            title: 'Warning',
            message: 'Transaction Failed, Please refresh page',
            type: 'warning'
          });
          this.button_.btn_loading = false
          return
        }
        this.button_.btn_loading = false
        this.paymentRequest(transactionhash, this.$store.state.web3.coinbase)
        this.$notify({
          title: 'Successfully Paid',
          message: 'You Paid:' + this.amount.toString() +"ether; Please Waiting For Connection URL Return. It may takes 30 Seconds.",
          type: 'success',
          duration: 0
        });
        this.pending = true
      })
    }
  },
  mounted () {
    console.log('dispatching getContractInstance')
    this.$store.dispatch('getContractInstance')
  }
}
</script>
<style scoped>
.light_box{
   box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
   font-size: 20px;
   text-align: center;
}
body {
    margin: 0;
}
.casino {
     margin-top: 50px;
     text-align:center;
}
#loader {
  width:150px;
}

.sc-condition{
  height: 50px;
  width: 100%;
}

</style>



